@import "styles/Variables.module.scss";

.mainContainer {
  display: flex;
}

.value {
  width: 68px;
  height: 56px;
  display: flex;
  justify-content: center;
  align-items: center;
  border-top: 1px solid $colorBg600;
  border-bottom: 1px solid $colorBg600;
  font-weight: $fwSemiBold;
  font-size: 20px;
  line-height: 32px;
}

%btn {
  width: 56px;
  height: 56px;
  background-color: $colorBg500;
  border: 1px solid $colorBg600;
  transition: $transAll;
  cursor: pointer;

  &:hover:not([disabled]),
  &:focus:not([disabled]) {
    border-color: $colorMain;
    border-width: 2px;
  }

  &:disabled,
  &[disabled] {
    cursor: default;
  }
}

.btnPlus {
  @extend %btn;
  border-radius: 0px 5px 5px 0px;

  img {
    /* To make plus icon spacing equal to minus icon spacings */
    padding: 7px;
  }
}

.btnMinus {
  @extend %btn;
  border-radius: 5px 0px 0px 5px;
}
